:root {
  --background: #0a0f1e;
  --background-secondary: #0d1425;
  --foreground: #ffffff;
  --foreground-secondary: #e0e7ff;
  --foreground-muted: #94a3b8;
  --primary-blue: #3b82f6;
  --accent-blue: #60a5fa;
  --light-blue: #93c5fd;
  --blue-glow: rgba(59, 130, 246, 0.3);
  --white-glow: rgba(255, 255, 255, 0.8);
  --white-glow-strong: rgba(255, 255, 255, 0.95);
  --card-bg: #1a1f35;
  --card-bg-hover: #1f2538;
  --border-color: rgba(255, 255, 255, 0.1);
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-geist-sans), -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 400;
  letter-spacing: -0.01em;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  letter-spacing: -0.02em;
  line-height: 1.2;
}
.ai-grid-bg {
  background-image: linear-gradient(rgba(59, 130, 246, 0.08) 1px, transparent 1px),
    linear-gradient(90deg, rgba(59, 130, 246, 0.08) 1px, transparent 1px);
  background-size: 50px 50px;
  background-position: 0 0;
}
.glow-white {
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3), 0 0 40px rgba(255, 255, 255, 0.15);
}
.glow-blue {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.4), 0 0 40px rgba(59, 130, 246, 0.2);
}
.glow-cyan {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.4), 0 0 40px rgba(59, 130, 246, 0.2);
}
.glow-purple {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.4), 0 0 40px rgba(59, 130, 246, 0.2);
}
.gradient-text {
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.3);
  font-weight: 700;
}
.gradient-text-blue {
  background: linear-gradient(135deg, #60a5fa, #93c5fd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--dark-bg);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--primary-cyan), var(--primary-purple));
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--accent-cyan), var(--accent-purple));
}
